import { createButterbar } from "../common/butterbar";
import { __ } from "./sakurairo_global";
const txt = [
    "# ",
    "# For commercial use, please contact the author for authorization. For non-commercial use, please indicate the source.",
    "# (License)-- 4.0  (CC BY-NC-SA 4.0)",
    "# (Author)" + mashiro_option.author_name,
    "# (URL)" + window.location.href,
    "# (Source)" + mashiro_option.site_name,
];
function setClipboardText(event, selectionTxt) {
    event.preventDefault();
    const htmlData = txt.join('<br>') + "<br><br>" + selectionTxt.replace(/\r\n/g, "<br>"), textData = txt.join('\n') + "\n\n" + selectionTxt.toString().replace(/\r\n/g, "\n");
    if (event.clipboardData) {
        event.clipboardData.setData("text/html", htmlData);
        event.clipboardData.setData("text/plain", textData);
    }
    else if (window.clipboardData) {
        //deprecate?
        return window.clipboardData.setData("text", textData);
    }
}
function copytext(e) {
    const selection = window.getSelection();
    if (selection) {
        const selectionText = selection.toString();
        if (selectionText.length > 30) {
            setClipboardText(e, selectionText);
            createButterbar(__(""), 1000);
        }
    }
}
/**
 * 
 */
export default function add_copyright() {
    if (mashiro_option.clipboardCopyright) {
        document.body.removeEventListener("copy", copytext);
        document.body.addEventListener("copy", copytext);
    }
}
